* {
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Lato', sans-serif;
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #333333;
  color: white;
  text-align: center;
  padding: 16px;
}

#container {
  box-sizing: border-box;
  min-height: 100vh;
  position: relative;
  padding-bottom: 64px;
}